.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.sticky-top-class {
  display: block;
  width: 100%;
  position: absolute;
  padding-top: 20px;
  height: 50px;
  background-color: #263238;
  color: #fff;
  text-align: center;
  font-size: 20px;
  bottom: 0;
}

.sticky-top-class a {
  color: inherit;
  text-decoration: none;
}

.body-container {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
}
#user-name path {
  animation: word-animate-fill 2s ease-in-out 4s forwards,
  word-animate-fill 2s ease-in-out 4s forwards;
}

